html {
  font-family: "Times New Roman", serif;
}

body {
  margin: 0;
  padding: 0;
}

.posts_list_tile {
  border: 1px solid #3D3333;
  box-shadow: #3D3333 3px 3px;
  cursor: pointer;
  margin: 0 0 1rem 0;
  padding: 0.5rem 1rem;
  background: #EEEEEE;
}

.posts_list_tile:hover {
  background: #FFFFFF;
}

.posts_list_tile:active {
  background: #DDCCCC;
}